import React, { Component } from "react";
import { Modal } from "antd";
import { inject, observer } from "mobx-react";
import { withTranslation } from "react-i18next";
import "./style.scss";
import { get } from "mobx";
import Nodata from "../../common/Widget/Nodata";
import Loading from "../../common/Widget/Loading";
import Operation from "../../common/Operation";
import { PureComponent } from "react";
import FrameCell from "../../common/FrameCell";

@observer
class FramesList extends PureComponent {
  constructor(props) {
    super(props);
    this.boxRef = React.createRef();
  }

  componentDidMount() {
    this.props.getData(this.boxRef.current.offsetWidth);

    window.addEventListener("resize", this.resize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => {
    this.props.resize(this.props.framelist, this.boxRef.current.offsetWidth);
  };

  render() {
    const { id, framelist, framelists, checkAllfn, click } = this.props;

    return (
      <>
        {framelist && (
          <Operation
            selectNum={framelists.length}
            dataNum={framelist.length}
            oncheck={checkAllfn}
          />
        )}
        <div className="frame_list" ref={this.boxRef}>
          {framelist ? (
            framelist.length > 0 ? (
              framelist.map((item) => {
                return (
                  <FrameCell
                    key={item.node}
                    id={id}
                    style={{ width: item.w, height: item.h }}
                    item={item}
                    name={get(item, "name")}
                    pitchOn={click}
                    noDetail={true}
                  />
                );
              })
            ) : (
              <Nodata />
            )
          ) : (
            <Loading />
          )}
        </div>
      </>
    );
  }
}

@withTranslation()
@inject("playdetail")
@observer
class Chooseframe extends Component {
  render() {
    const {
      visible,
      title,
      framelist,
      onOk,
      onCancel,
      id,
      click,
      checkAllfn,
      framelists,
      getData,
      inner,
    } = this.props;
    const { t } = this.props;
    return (
      <Modal
        title={title}
        visible={visible}
        getContainer={false}
        destroyOnClose={true}
        width={inner ? "100%" : 1100}
        okText={t("Determine")}
        cancelText={t("Cancel")}
        onOk={onOk}
        onCancel={onCancel}
        centered
      >
        <FramesList
          id={id}
          framelist={framelist}
          framelists={framelists}
          checkAllfn={checkAllfn}
          click={click}
          getData={getData}
          resize={this.props.playdetail.framesResize}
        />
      </Modal>
    );
  }
}

export default Chooseframe;
