import React, { PureComponent } from "react";
import { withTranslation } from "react-i18next";
import { Icon, Dropdown, Button, Menu, Tabs } from "antd";
import Upinput from "../../common/Upinput";

const { TabPane } = Tabs;

@withTranslation()
class SmallscreenHead extends PureComponent {
  state = {};
  render() {
    const {
      t,
      name,
      filenum,
      frameListNum,
      go,
      info_show,
      onChange,
      uploading,
      cloudAlbumUpload,
      addPlaylistClick,
      Ikey,
      chooseframe,
    } = this.props;
    const menu = (
      <Menu>
        <Menu.Item key="0">
          <div className="cloudAlbumUpload">
            <Icon type="plus" style={{ fontSize: "22px" }} />
            <Upinput className="upphoto-btn up_btn" onupload={uploading} />
          </div>
        </Menu.Item>
        <Menu.Item key="1">
          <span className="cloudAlbumUpload" onClick={cloudAlbumUpload}>
            <Icon type="picture" style={{ fontSize: "22px" }} />
            <span>{t("albumAdd")}</span>
          </span>
        </Menu.Item>
        <Menu.Item key="2">
          <span className="cloudAlbumUpload" onClick={addPlaylistClick}>
            <Icon type="unordered-list" style={{ fontSize: "22px" }} />
            <span>{t('Addplaylist')}</span>
          </span>
        </Menu.Item>
      </Menu>
    );

    return (
      <div className="head_s_inner">
        <div className="head_s_back">
          <p>
            <span className="ellipsis" onClick={go}>
              <Icon type="left" />
              {name}
            </span>
          </p>
          {Ikey === "2" ? (
            <div className="choose-frame">
              <span onClick={chooseframe}>{t("Chooseframe")}</span>
            </div>
          ) : (
            <Dropdown overlay={menu} trigger={["hover", "click"]} size="large">
              <Button type="primary">
                {t("Uploadphotos")}
                <Icon type="down" />
              </Button>
            </Dropdown>
          )}
        </div>
        <div className="head_s_tabr">
          <Tabs
            defaultActiveKey={Ikey}
            tabBarGutter={0}
            tabBarStyle={{ marginBottom: "0" }}
            onChange={onChange}
            // type="card"
          >
            <TabPane
              tab={
                <span>
                  <i className="iconfont icon">&#xe785;</i>
                  {t("Photo")}({filenum})
                </span>
              }
              key="1"
            ></TabPane>
            <TabPane
              tab={
                <span>
                  <i className="iconfont icon">&#xe607;</i>
                  {t("photoframe")}({frameListNum})
                </span>
              }
              key="2"
            ></TabPane>
          </Tabs>
          <Button
            className="head_s_info_btn"
            type="primary"
            icon="info"
            onClick={info_show}
          />
        </div>
      </div>
    );
  }
}

export default SmallscreenHead;
