import React, { Component } from "react";
import { Icon, message } from "antd";
import { Player, BigPlayButton } from "video-react";
import { withTranslation } from "react-i18next";

@withTranslation()
class VideoPlayer extends Component {
  state = {
    inner: false,
    load: false,
  };
  componentDidMount() {
    this.resize();
    this.player.subscribeToStateChange(this.handleStateChange.bind(this));
    window.addEventListener("resize", this.resize);
  }

  componentWillUnmount(){
    window.removeEventListener("resize", this.resize);
  }

  UNSAFE_componentWillReceiveProps(Props) {
    if (this.props.src !== Props.src) {
      this.setState({ load: false });
    }
  }

  resize = () => {
    if (window.innerWidth < 900) {
      if (!this.state.inner) {
        this.setState({ inner: true });
      }
    } else {
      if (this.state.inner) {
        this.setState({ inner: false });
      }
    }
  };

  handleStateChange(state) {
    this.player.video.handleError = () => {
      message.error(this.props.t("Videofailed"));
    };
    if (this.state.load) {
      return;
    }
    if (state.readyState === 4) {
      this.setState({
        load: true,
      });
    }
  }
  render() {
    const { poster, src } = this.props;
    return (
      <div id="VIDEOBOX">
        <Icon
          className="icon-load"
          type="loading"
          style={{
            fontSize: 70,
            display: `${this.state.load ? "none" : "block"}`,
          }}
          spin
        />
        <Player
          ref={(player) => {
            this.player = player;
          }}
          playsInline
          fluid={false}
          poster={poster}
          src={src}
          preload="auto"
          autoPlay
          height={this.state.inner ? window.innerHeight / 2 : 600}
          width={this.state.inner ? window.innerHeight : 900}
          className="p_video"
        >
          <BigPlayButton position="center" />
        </Player>
      </div>
    );
  }
}

export default VideoPlayer;
