import React, { PureComponent } from "react";
import { get } from "mobx";
import { getFile, strRep, os } from "../util/index";
import { Tooltip, Menu, Dropdown, Icon } from "antd";
import { withTranslation } from "react-i18next";
import "./index.scss";
import { observer } from "mobx-react";

@withTranslation()
@observer
class FrameCell extends PureComponent {
  constructor(props) {
    super(props);
    this.fileRef = React.createRef();
  }

  render() {
    const {
      t,
      item,
      name,
      id,
      style,
      goRahmendetails,
      pitchOn,
      morelist,
      click,
      uploading,
      framePlay,
      selectNum,
      noDetail,
    } = this.props;
    const menu = morelist && (
      <Menu
        onClick={(e) => {
          click(e, item);
        }}
      >
        {morelist.map((el, i) => {
          if (el.type) {
            return (
              <Menu.Item key={i}>
                <div className="upfile">
                  <span className="ListCell_menuItem">
                    <Icon
                      style={{ fontSize: "16px", marginRight: "10px" }}
                      type={el.icon}
                    />
                    {el.name}
                  </span>
                  <input
                    type="file"
                    ref={this.fileRef}
                    title=""
                    accept="image/jpg,image/bmp,image/jpeg,image/png"
                    onChange={(e) => {
                      uploading(e, item.node, () => {
                        this.fileRef.current.value = null;
                      });
                    }}
                  />
                </div>
              </Menu.Item>
            );
          } else {
            return (
              <Menu.Item key={i}>
                <span className="ListCell_menuItem">
                  <Icon
                    style={{ fontSize: "16px", marginRight: "10px" }}
                    type={el.icon}
                    theme="filled"
                  />
                  {el.name}
                </span>
              </Menu.Item>
            );
          }
        })}
      </Menu>
    );

    return (
      <div className="FrameCell" style={style}>
        <div
          className={`rahmen-list ${
            get(item, "check") ? "rahmen-list-check" : ""
          }`}
          onClick={(e) => {
            if (noDetail) {
              pitchOn(item);
            } else {
              if (selectNum) {
                pitchOn(e, item.node);
              } else {
                goRahmendetails && goRahmendetails(item);
              }
            }
          }}
        >
          {pitchOn && (
            <span
              style={
                os().isPhone || os().isAndroid || os().isTablet
                  ? { visibility: "inherit" }
                  : {}
              }
              className="icon-check-s treeNodeUnselectable"
              onClick={(e) => {
                if (goRahmendetails) {
                  pitchOn(e, item.node);
                }
              }}
            >
              <i
                className="iconfont"
                style={get(item, "check") ? {} : { visibility: "hidden" }}
              >
                &#xe62d;
              </i>
            </span>
          )}
          <div className="rahmen-cover">
            <div
              className="cover-img"
              style={{
                width: `${item.w * 0.53}px`,
                height: `${item.w * 0.7}px`,
              }}
            >
              <span
                className="back-img"
                style={
                  item.profile
                    ? {
                        backgroundImage: `url("${getFile.file_s(
                          item.profile.id
                        )}")`,
                        backgroundSize: "100% 100%",
                      }
                    : {}
                }
              ></span>
            </div>
            {framePlay && (
              <div className="frame-ringht-500">
                <Tooltip
                  placement="left"
                  overlayClassName="tooltip"
                  title={t("playtoframe")}
                >
                  <Icon
                    onClick={() => {
                      framePlay([item.node]);
                    }}
                    type="play-square"
                    theme="filled"
                  />
                </Tooltip>
              </div>
            )}
          </div>
          <div className="rahmen-name-series">
            <p className="name_info">
              <Tooltip
                placement="bottom"
                overlayClassName="tooltip"
                title={get(item, "name")}
              >
                <span className="name ellipsis">{name}</span>
              </Tooltip>
              {morelist ? (
                <span
                  className="admin"
                  style={
                    item.is_admin
                      ? { visibility: "initial" }
                      : { visibility: "hidden" }
                  }
                >
                  {t("Admin")}
                </span>
              ) : (
                item.playlist_id === id && (
                  <span className="admin">{t("default")}</span>
                )
              )}
            </p>
            <div className="rahmen_bottom">
              <p className="series">SN : {strRep(item.serial)} </p>
              {morelist && (
                <div className="more">
                  <Dropdown
                    overlay={menu}
                    placement="bottomRight"
                    trigger={["hover", "click"]}
                  >
                    <Icon
                      onClick={(event) => event.stopPropagation()}
                      type="ellipsis"
                      style={{ fontSize: "20px" }}
                    />
                  </Dropdown>
                </div>
              )}
            </div>

            {framePlay && (
              <div className="frame-ringht">
                <Tooltip
                  placement="left"
                  overlayClassName="tooltip"
                  title={t("playtoframe")}
                >
                  <Icon
                    onClick={() => {
                      framePlay([item.node]);
                    }}
                    type="play-square"
                    theme="filled"
                  />
                </Tooltip>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default FrameCell;
