import React from "react";
import { Menu, Dropdown, Button, Icon, message, Modal } from "antd";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import dayjs from "dayjs";
import { FixedSizeGrid as Grid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import { get, observable, remove, set } from "mobx";
import "./style.scss";
import Thumbnail from "../../common/Thumbnail";
import Upinput from "../../common/Upinput";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import Chooseframe from "./Chooseframe";
import Information from "../../common/Information";
import { wsget } from "../../ws";
import event from "../../util/event";
import Goblack from "../../common/Goblack";
import Toframes from "./Toframes";
import i18next from "i18next";
import Operation from "../../common/Operation";
import DropUp from "../../common/DropUp";
import SmallscreenHead from "./SmallscreenHead";
import { getFile, os, selfAdaption } from "../../util/index";
import CustomModal from "../../common/CustomModal";
import ListCell from "../../common/ListCell";
import ThumbnailCom from "../../common/Thumbnail/ThumbnailCom";
import ImageGallery from "../../common/ImageGallery";


const { confirm } = Modal;

@withTranslation()
@inject("playlist", "uploadFiles", "login", "rahmen", "playdetail")
@observer
class Playlistdetails extends React.Component {
  constructor(props) {
    super(props);
    this.renameRef = React.createRef();
    this.imgRef = React.createRef();
    this.frameRef = React.createRef();
    this.addPlaylistRef = React.createRef();
  }
  @observable inner = false;
  @observable selectInner = false;
  @observable dropVisible = false;
  @observable num = 100; //一页加载个数
  @observable sortableInstance = null;
  @observable init = false; //初始化
  detail_list = { list: [] }; //用来展示列表详情
  detail_list_2 = { list: [] }; //操作的列表详情
  checkAll = false; //是否全选
  @observable removeFileids = []; //选中的图片的列表
  @observable bImg_visible = false; //大图modul
  @observable defaultValue = ""; //图片描述
  @observable imgfile = {}; //大图文件
  @observable imgidx = null; //切换图片的标识
  @observable set_visible = false; //查看绑定相框显隐开关
  @observable playlistFrames_load = false; //查看绑定相框loading
  @observable frameList = []; //绑定相框de列表
  @observable frameListNum = 0; //绑定相框个数
  @observable info_visible = false; //列表信息显隐
  @observable infoObj = null; //列表信息数据
  @observable dragIndex = null; //列表信息数据
  checkidxArr = [];
  @observable name = "";
  @observable frame_count = 0;
  @observable id = null;
  @observable list = []; //渲染的数据
  @observable page = 0; //第一条数据
  @observable hasMoreItems = true;
  @observable number = this.num;
  @observable frames_visible = false; //选择相框显隐
  @observable filenum = 0; //文件数量
  @observable created = null; //创建时间
  @observable updated = null; //更新时间
  @observable h = 0; //更新时间
  @observable w = 0; //更新时间
  flag = true; //拖拽保存列表的标记

  // @observable initialData = [];
  @observable initialData = [];
  @observable key = "1";

  @observable otherVisible = false;
  @observable okBtnDis = true;
  @observable addPlistInit = true;
  @observable detailList = [];
  @observable addPlaylist = [];
  @observable renderItem = 0;
  @observable addItemClick = null;
  @observable addSelNum = 0;
  @observable addTitle = 0;

  async componentDidMount() {
    try {
      const { id } = this.props.match.params;
      const { name, created, frame_count } = this.props.playlist.urlData;
      this.getInner();
      if (!name || !created) {
        const playArr = await wsget("playlist_list", null);
        const pos = playArr.findIndex((item) => item.id === Number(id));
        if (pos === -1) {
          this.props.history.push(`/playlist`);
        }
        this.name = playArr[pos].name;
        this.created = playArr[pos].created;
        this.frame_count = playArr[pos].frame_count;
      } else {
        this.name = name;
        this.created = created;
        this.frame_count = frame_count;
      }
      this.id = Number(id);
      this.get();
      this.props.playdetail.getData(this.id, this.getWH, this.getH, this.w);

      window.addEventListener("resize", this.myresize);
      // window.addEventListener('resize', this.myresize2);

      this.wsinit = event.on("wsinit", (res) => {
        if (res === 1) {
          if (!this.props.playdetail.init) {
            this.props.playdetail.getData(
              this.id,
              this.getWH,
              this.getH,
              this.w
            );
          }
        }
      });
    } catch (error) {
      this.props.history.push("/playlist");
    }

    document.addEventListener("dragenter", this.ondragenter);

    document.addEventListener("dragleave", this.ondragleave);

    document.addEventListener("dragover", this.ondragover);

    document.addEventListener("drop", this.ondragover);
  }

  componentWillUnmount() {
    event.rmName("framesNum");
    event.rmName("rmCheck");
    window.removeEventListener("resize", this.myresize);
    // window.removeEventListener("resize", this.myresize2);
    document.removeEventListener("dragenter", this.ondragenter);
    document.removeEventListener("dragleave", this.ondragleave);
    document.removeEventListener("dragover", this.ondragover);
    document.removeEventListener("drop", this.ondragover);
    event.rm(this.wsinit);
    this.props.playdetail.clearData();
  }

  //拖拽上传
  ondragenter = (e) => {
    if (this.key === "1") {
      this.elemetnNode = e.target;
      if (!this.dropVisible) {
        this.dropVisible = true;
      }
    }
  };
  ondragleave = (e) => {
    if (this.elemetnNode === e.target && this.key === "1") {
      this.dropVisible = false;
    }
  };
  ondragover = (e) => {
    e.preventDefault(); //阻止浏览器读取文件
  };
  acceptedFiles = (Files) => {
    const { id } = this;
    this.dropVisible = false;
    this.props.uploadFiles.upFile(Files, id, null);
  };
  //end

  myresize = () => {
    this.getInner();
    this.get();
    this.props.playdetail.resize(this.getWH);
    this.props.playdetail.resize2(this.w, this.props.playdetail.frameList);
  };

  get = () => {
    if (this.key === "1") {
      this.w = this.imgRef.current.offsetWidth;
      this.h = this.imgRef.current.offsetHeight;
    } else {
      this.w = this.frameRef.current.offsetWidth;
      this.h = this.frameRef.current.offsetHeight;
    }
  };

  getInner = () => {
    if (window.innerWidth < 597) {
      if (!this.inner) {
        this.inner = true;
      }
    } else {
      if (this.inner) {
        this.inner = false;
      }
    }
    if (window.innerWidth < 1100) {
      if (!this.selectInner) {
        this.selectInner = true;
      }
    } else {
      if (this.selectInner) {
        this.selectInner = false;
      }
    }
  };

  //计算宽高
  getWH = (flag) => {
    try {
      const { wi, hi, colNum } = selfAdaption(this.w, 165, 150, 100, 100, 597);
      this.childW = wi;
      this.childH = hi;
      this.columnCount = colNum;
      if (!flag) {
        this.getH();
      }
      return this.columnCount;
    } catch (error) {
      console.log(error);
      // this.getWH();
      this.props.history.push("/playlist");
    }
  };

  getH = () => {
    const { initialData } = this.props.playdetail;
    this.rowCount = initialData.length / this.columnCount;
  };

  //详情显示
  info_show = () => {
    const infoObj = [
      {
        id: 1,
        icon: "file-image",
        txt: "document",
        data: this.props.playdetail.filenum,
      },
      {
        id: 2,
        icon: "file-image",
        txt: "BindingFrame",
        data: this.props.playdetail.frameListNum
          ? this.props.playdetail.frameListNum
          : this.frame_count,
      },
      {
        id: 3,
        icon: "clock-circle",
        txt: "CreationTime",
        data: dayjs(this.created).format(this.props.t("LL")),
      },
      {
        id: 4,
        icon: "clock-circle",
        txt: "LastUpdate",
        data: dayjs(this.props.playdetail.updated).format(this.props.t("LL")),
      },
    ];
    this.info_visible = true;
    this.infoObj = infoObj;
  };

  //上传
  uploading = (e, reset) => {
    const { id } = this;
    this.props.uploadFiles.upFile(e.target.files, id, reset);
  };

  //返回
  goblack = () => {
    this.props.history.goBack();
  };

  //删除
  remove_img = () => {
    confirm({
      title: "Are you sure to delete these pictures or videos?",
      content: "Deleted images and videos can be recovered at the recycle bin",
      okText: "Yes",
      okType: "danger",
      cancelText: "No",
      onOk: () => {
        this.props.playdetail.remove_img(this.id);
      },
      onCancel() { },
    });
  };

  //选择相框显示
  chooseframe = () => {
    this.frames_visible = true;
  };
  //播放到相框
  frameonPlay = (frames) => {
    if (!this.props.playdetail.initialData.length > 0) {
      message.warning(i18next.t("Listempty"));
      return;
    }
    wsget("playlist_play_in_frame", { id: this.id, frames }, 1);
  };
  handleOk = (e) => {
    this.frames_visible = false;
    this.props.playdetail.handleOk();
  };
  handleCancel = (e) => {
    this.frames_visible = false;
    this.props.playdetail.framesno();
  };
  //选择相框end

  onchange = (key) => {
    this.key = key;
    if (key === "2") {
      this.props.playdetail.getFrames(this.id, this.w);
    }
  };

  myresize2 = () => {
    this.props.playdetail.resize2(
      this.frameRef.current.offsetWidth,
      this.props.playdetail.frameList
    );
  };

  frameGetData = (width) => this.props.playdetail.getframes(this.id, width);

  //云相册添加
  cloudAlbumUpload = () => {
    this.addTitle = 1;
    this.otherVisible = true;
    this.addPlistInit = false;
    wsget("album_list", null).then((arr) => {
      const { wi, hi, colNum } = selfAdaption(
        this.addPlaylistRef.current.offsetWidth,
        200,
        220,
        180,
        200
      );
      this.addChildW = wi;
      this.addChildH = hi;
      this.addColnum = colNum;
      const countRow = arr.length / this.addColnum;
      this.addRowCount = Math.ceil(countRow);
      if (arr.length % this.addColnum !== 0) {
        const leng = this.addColnum * this.addRowCount - arr.length;
        for (let index = 0; index < leng; index++) {
          arr.push({ true: true });
        }
      }
      this.addPlaylist = arr;
      this.addPlistInit = true;
    });
  };

  //playlist添加
  addPlaylistClick = () => {
    this.addTitle = 0;
    this.otherVisible = true;
    this.addPlistInit = false;

    wsget("playlist_list", null).then((data) => {
      const arr = data.filter((el) => el.id !== this.id);
      const { wi, hi, colNum } = selfAdaption(
        this.addPlaylistRef.current.offsetWidth,
        200,
        220,
        180,
        200
      );
      this.addChildW = wi;
      this.addChildH = hi;
      this.addColnum = colNum;
      const countRow = arr.length / this.addColnum;
      this.addRowCount = Math.ceil(countRow);
      if (arr.length % this.addColnum !== 0) {
        const leng = this.addColnum * this.addRowCount - arr.length;
        for (let index = 0; index < leng; index++) {
          arr.push({ true: true });
        }
      }
      this.addPlaylist = arr;
      this.addPlistInit = true;
    });
  };

  playListRender = ({ columnIndex, data, rowIndex, style }) => {
    const item = data[rowIndex * this.addColnum + columnIndex];
    return (
      <ListCell
        style={style}
        timeName={`${this.props.t("CreationTime")}：`}
        item={item}
        goPlaylistdetails={this.goP_listdetail}
        album={this.addTitle}
        frameNumDis={this.addTitle}
      />
    );
  };

  goP_listdetail = async (item) => {
    this.addPlistInit = false;
    this.renderItem = 1;
    this.addItemClick = item;
    let data = [];

    if (this.addTitle) {
      data = await wsget("album_detail_mini", item.id);
    } else {
      const res = await wsget("playlist_detail2", item.id);
      data = res.list || [];
    }
    const { wi, hi, colNum } = selfAdaption(
      this.addPlaylistRef.current.offsetWidth,
      130,
      130,
      100,
      100,
      597
    );
    this.addChildW = wi;
    this.addChildH = hi;
    this.addColnum = colNum;
    const countRow = data.length / this.addColnum;
    this.addRowCount = Math.ceil(countRow);
    if (data.length % this.addColnum !== 0) {
      const leng = this.addColnum * this.addRowCount - data.length;
      for (let index = 0; index < leng; index++) {
        data.push({ true: true });
      }
    }
    this.detailList = data;
    this.okBtnDis = false;
    this.addPlistInit = true;
  };

  renderDetails = ({ columnIndex, data, rowIndex, style }) => {
    const item = data[rowIndex * this.addColnum + columnIndex];
    try {
      if (get(item, "true")) {
        return <div style={style}></div>;
      } else {
        return (
          <ThumbnailCom
            item={item}
            style={style}
            num={1}
            checkclick={this.checkclick}
            b_img_show={this.checkclick}
            index={rowIndex * this.addColnum + columnIndex}
            os={os().isPhone || os().isAndroid || os().isTablet}
            imgBackground={{
              background: `#d3d3d3 url(${getFile.file_s(
                item.id
              )}) center center no-repeat`,
              backgroundSize: "cover",
            }}
          />
        );
      }
    } catch (error) {
      return <div style={style}></div>;
    }
  };

  addGoback = () => {
    this.okBtnDis = true;
    const list = this.addPlaylist.filter((item) => {
      return !item.true;
    });
    const { wi, hi, colNum } = selfAdaption(
      this.addPlaylistRef.current.offsetWidth,
      200,
      220,
      180,
      200
    );
    this.addChildW = wi;
    this.addChildH = hi;
    this.addColnum = colNum;
    const countRow = list.length / this.addColnum;
    this.addRowCount = Math.ceil(countRow);
    this.addSelNum = 0;
    this.renderItem = 0;
  };

  checkclick = (e, item) => {
    e.stopPropagation();
    if (item.check) {
      remove(item, "check");
      this.addSelNum -= 1;
    } else {
      set(item, { check: true });
      this.addSelNum += 1;
    }
  };

  addCheckAll = () => {
    const list = this.detailList.filter((item) => {
      return !item.true;
    });
    if (this.addSelNum === list.length) {
      this.detailList.forEach((item) => {
        if (item.id) {
          remove(item, "check");
        }
      });
      this.addSelNum = 0;
    } else {
      this.addSelNum = 0;
      this.detailList.forEach((item) => {
        if (item.id) {
          set(item, { check: true });
          this.addSelNum += 1;
        }
      });
    }
  };

  addInvert = () => {
    this.addSelNum = 0;
    this.detailList.forEach((item) => {
      if (item.id) {
        if (item.check) {
          remove(item, "check");
        } else {
          set(item, { check: true });
          this.addSelNum += 1;
        }
      }
    });
  };

  addhandleOk = () => {
    const list = [];
    this.detailList.forEach((item) => {
      if (item.check) {
        const obj = {
          id: item.id,
        };
        if (item.cpt) {
          obj.cpt = item.cpt;
        }
        list.push(obj);
      }
    });
    if (!list.length) {
      message.error(this.props.t("Noselectedpic"));
      return;
    }
    this.addCancel();
    const data = {
      id: this.id,
      list,
      index: 0,
    };
    wsget("playlist_save_to", data, 1).then((res) => {
      this.props.playdetail.getData(this.id, this.getWH, this.getH, this.w);
    });
  };

  addCancel = () => {
    this.okBtnDis = true;
    this.otherVisible = false;
    this.addItemClick = null;
    this.addSelNum = 0;
    this.renderItem = 0;
  };

  render() {
    const { t } = this.props;
    const {
      frames_visible,
      info_visible,
      infoObj,
      id,
      name,
      created,
      frame_count,
    } = this;
    const {
      init,
      initialData,
      oncheckAll,
      filenum,
      bImg_visible,
      closeDialog,
      imgfile,
      onPiece,
      nextPage,
      addCpt,
      frames,
      checkFrames,
      frameListNum,
      frameList,
      updated,
      invert,
      moveTop,
      moveLast,
      checkAllfn,
      framelists,
      cancelAll,
      num,
      frameinit,
    } = this.props.playdetail;
    const frameNum = frameListNum ? frameListNum : frame_count;

    const menu = (
      <Menu>
        <Menu.Item key="0">
          <div className="cloudAlbumUpload">
            <Icon type="plus" style={{ fontSize: "22px" }} />
            <Upinput className="upphoto-btn up_btn" onupload={this.uploading} />
          </div>
        </Menu.Item>
        <Menu.Item key="1">
          <span className="cloudAlbumUpload" onClick={this.cloudAlbumUpload}>
            <Icon type="picture" style={{ fontSize: "22px" }} />
            <span>{t("albumAdd")}</span>
          </span>
        </Menu.Item>
        <Menu.Item key="2">
          <span className="cloudAlbumUpload" onClick={this.addPlaylistClick}>
            <Icon type="unordered-list" style={{ fontSize: "22px" }} />
            <span>{t("Addplaylist")}</span>
          </span>
        </Menu.Item>
      </Menu>
    );

    const empty = this.renderItem
      ? this.detailList.length
      : this.addPlaylist.length;

    return (
      <div className="playlistdetails">
        {init ? (
          this.inner ? (
            <SmallscreenHead
              Ikey={this.key}
              name={name}
              filenum={filenum}
              frameListNum={frameNum}
              go={this.goblack}
              info_show={this.info_show}
              onChange={this.onchange}
              uploading={this.uploading}
              cloudAlbumUpload={this.cloudAlbumUpload}
              addPlaylistClick={this.addPlaylistClick}
              chooseframe={this.chooseframe}
            />
          ) : (
              <div className="playlist-header">
                <Goblack go={this.goblack} name={name} />
                <div className="p-details-information">
                  {this.key === "2" ? (
                    <div className="choose-frame">
                      <span onClick={this.chooseframe}>{t("Chooseframe")}</span>
                    </div>
                  ) : (
                      <Dropdown
                        overlay={menu}
                        trigger={["hover", "click"]}
                        size="large"
                      >
                        <Button type="primary">
                          {t("Uploadphotos")}{" "}
                          <Icon style={{ fontSize: "14px" }} type="down" />
                        </Button>
                      </Dropdown>
                    )}
                  <div
                    className={`file-num ${this.key === "1" ? "befort" : ""}`}
                    onClick={() => {
                      this.onchange("1");
                    }}
                  >
                    <div className="p-d-i-icon">
                      <i className="iconfont icon">&#xe785;</i>
                    </div>
                    <div className="num-text">
                      <p className="text">{t("Photo")}</p>
                      <p className="num">{filenum}</p>
                    </div>
                  </div>
                  <div
                    className={`frame-num ${this.key === "2" ? "befort" : ""}`}
                    onClick={() => {
                      this.onchange("2");
                    }}
                  >
                    <div className="p-d-i-icon">
                      <i className="iconfont icon">&#xe607;</i>
                    </div>
                    <div className="num-text">
                      <p className="text">{t("photoframe")}</p>
                      <p className="num">{frameNum}</p>
                    </div>
                  </div>
                  <div className="Time">
                    <div className="creation-time">
                      <div className="p-d-i-icon">
                        <span className="creation-time-icon"></span>
                      </div>
                      <div className="num-text">
                        <p className="text">{t("CreationTime")}</p>
                        <p className="num">
                          {created && dayjs(created).format(t("LL"))}
                        </p>
                      </div>
                    </div>
                    <div className="last-update">
                      <div className="p-d-i-icon">
                        <span className="last-update-icon"></span>
                      </div>
                      <div className="num-text">
                        <p className="text">{t("LastUpdate")}</p>
                        <p className="num">
                          {updated &&
                            dayjs(updated ? updated : created).format(t("LL"))}
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="playlist-set">
                    <div className="playlist-msg-icon" onClick={this.info_show}>
                      <Icon type="exclamation" />
                    </div>
                  </div>
                </div>
              </div>
            )
        ) : null}

        {init && this.key === "1" && (
          <Operation
            selectNum={num}
            dataNum={initialData.filter((el) => el.id).length}
            oncheck={oncheckAll}
            invert={invert}
            remove={this.remove_img}
            moveTop={() => {
              moveTop(id);
            }}
            moveLast={() => {
              moveLast(id);
            }}
            cancelAll={cancelAll}
          />
        )}

        <div
          className="playlistdetails-main"
          style={this.key === "1" ? {} : { overflowY: "auto" }}
        >
          <div
            className="playlistdetails-main-left"
            style={this.key === "1" ? {} : { display: "none" }}
          >
            <div ref={this.imgRef} className="imglist-inner">
              {init ? (
                initialData.length > 0 ? (
                  <AutoSizer>
                    {({ height, width }) => (
                      <Grid
                        className="Grid"
                        itemData={initialData}
                        rowCount={this.rowCount}
                        columnCount={this.columnCount}
                        columnWidth={this.childW}
                        height={height}
                        rowHeight={this.childH}
                        width={width}
                      >
                        {Thumbnail}
                      </Grid>
                    )}
                  </AutoSizer>
                ) : (
                    <Nodata />
                  )
              ) : (
                  <Loading />
                )}
            </div>
          </div>

          <div
            ref={this.frameRef}
            className="toframes"
            style={this.key === "2" ? {} : { display: "none" }}
          >
            <Toframes
              framelist={frameList}
              id={id}
              initload={frameinit}
              framePlay={this.frameonPlay}
            />
          </div>
        </div>

        {this.dropVisible && (
          <DropUp
            title={t("UploadToList")}
            acceptedFiles={this.acceptedFiles}
          />
        )}

        {
          bImg_visible && (
            <ImageGallery
              id={imgfile.id}
              cpt={imgfile.cpt}
              ext={imgfile.ext}
              closeDialog={closeDialog}
              onPiece={onPiece}
              nextPage={nextPage}
              blur={addCpt}
            />
          )
        }

        <Chooseframe
          title={t("Chooseframe")}
          visible={frames_visible}
          framelist={frames}
          id={id}
          click={checkFrames}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          checkAllfn={checkAllfn}
          framelists={framelists}
          getData={this.frameGetData}
          inner={this.selectInner}
        />

        {/* 从相册或者播放列表添加 */}
        <CustomModal
          visible={this.otherVisible}
          title={this.addTitle ? t("albumAdd") : t("Addplaylist")}
          okBtnDis={this.okBtnDis}
          handleOk={this.addhandleOk}
          handleCancel={this.addCancel}
        >
          <>
            {this.renderItem ? (
              <div style={{ position: "relative" }}>
                <Goblack
                  go={this.addGoback}
                  name={this.addItemClick.name}
                  style={{ paddingRight: "180px", paddingLeft: "10px" }}
                />
                <div className="add_operation">
                  <Operation
                    selectNum={this.addSelNum}
                    dataNum={this.detailList.filter((el) => el.id).length}
                    oncheck={this.addCheckAll}
                    invert={this.addInvert}
                    style={{ border: "0", padding: "0" }}
                  />
                </div>
              </div>
            ) : null}
            <div
              style={{ height: this.renderItem ? "61vh" : "65vh" }}
              className="add_toBum_main"
              ref={this.addPlaylistRef}
            >
              {this.addPlistInit ? (
                empty ? (
                  <AutoSizer>
                    {({ height, width }) => (
                      <Grid
                        className="Grid"
                        itemData={
                          this.renderItem ? this.detailList : this.addPlaylist
                        }
                        rowCount={this.addRowCount}
                        columnCount={this.addColnum}
                        columnWidth={this.addChildW}
                        height={height}
                        rowHeight={this.addChildH}
                        width={width}
                      >
                        {this.renderItem
                          ? this.renderDetails
                          : this.playListRender}
                      </Grid>
                    )}
                  </AutoSizer>
                ) : (
                    <Nodata />
                  )
              ) : (
                  <Loading />
                )}
            </div>
          </>
        </CustomModal>

        <Information
          visible={info_visible}
          data={infoObj}
          handleCancel={() => {
            this.info_visible = false;
          }}
        />
      </div>
    );
  }
}

export default Playlistdetails;
