import React from 'react';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import { Tooltip } from 'antd';
import { os } from '../../util/index';

@withTranslation() @inject('uploadFiles') @observer
class Upinput extends React.Component {
  constructor(props) {
    super(props);
    this.fileRef = React.createRef();
  }

  upload = (e) => {
    this.props.onupload(e,() => {
      this.fileRef.current.value = null;
    })
  }

  render() {
    const { className, t } = this.props;
    const tooltipNode = () => {
      return (
        <ul>
          <li>{`1、${t('picsizeMore')}`}</li>
          <li>{`2、${t('VsizeMore')}`}</li>
        </ul>
      )
    }
    const ps =
      os().isPhone || os().isAndroid || os().isTablet ? { visible: false } : {};
    return (
      <Tooltip placement="right" overlayClassName='tooltip' title={tooltipNode} {...ps}>
        <div className={className ? className : null}>
          {t('Uploadphotos')}
          <input
            type="file"
            ref={this.fileRef}
            title=''
            accept="image/bmp,image/gif,image/jpeg,image/png,video/mp4,video/avi,video/m3u8,video/flv,video/rmvb"
            multiple='multiple'
            onChange={this.upload}
          />
        </div>
      </Tooltip>
    );
  }
}

export default Upinput;