import React from "react";
import { observable } from "mobx";
import { getFile, imgLoad } from "../../util/index";
import { Icon, Modal } from "antd";
import VideoPlayer from "../../common/VideoPlayer";
import { observer } from "mobx-react";
import i18next from "i18next";
import './style.scss';

const videoReg = /(mp4|avi|rmvb|rm|wmv|mov|mpg|mpeg|flv|mkv|3gp|webm|ts|m3u8)$/;

@observer
class ImageGallery extends React.Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
    this.imgRef = React.createRef();
    this.state = {
      load: false,
      X: 0,
      Y: 0,
      url: "",
      cptModle: false,
    };
    this.moving = false;
    this.lastX = null;
    this.lastY = null;
    this.times = 1;
    window.onmouseup = (e) => {
      this.onMouseUp(e);
    };
    window.onmousemove = (e) => {
      this.onMouseMove(e);
    };
  }
  @observable scale = 1;
  @observable filg = false;
  @observable width = 0;
  @observable height = 0;

  componentDidMount() {
    const { id } = this.props;

    imgLoad(getFile.file_s(id), (_, img) => {
      if (!this.isUnmount) {
        const w = img.width / 2;
        const h = img.height / 2;
        this.setState({ X: -w, Y: -h });
      }
    });
    imgLoad(getFile.file_b(id), (url) => {
      if (!this.isUnmount) {
        this.setState({ url, load: true }, () => {
          let w = this.imgRef.current.offsetWidth / 2;
          let h = this.imgRef.current.offsetHeight / 2;
          this.setState({ X: -w, Y: -h });
        });
      }
    });
  }

  componentWillUnmount() {
    this.isUnmount = true;
  }

  UNSAFE_componentWillReceiveProps(Props) {
    if (this.props.id !== Props.id) {
      this.scale = 1;
      this.times = 1;
      this.setState({ load: false });
      imgLoad(getFile.file_s(Props.id), (_, img) => {
        if (!this.isUnmount) {
          const w = img.width / 2;
          const h = img.height / 2;
          this.setState({ X: -w, Y: -h });
        }
      });
      imgLoad(getFile.file_b(Props.id), (url) => {
        if (!this.isUnmount) {
          this.setState({ url, load: true }, () => {
            let w = this.imgRef.current.offsetWidth / 2;
            let h = this.imgRef.current.offsetHeight / 2;
            this.setState({ X: -w, Y: -h });
          });
        }
      });
    }
  }

  onMouseDown(e) {
    if (!videoReg.test(this.props.ext)) {
      e.stopPropagation();
      e.preventDefault();
      this.filg = true;
      this.moving = true;
    }
  }

  onMouseUp() {
    this.filg = false;
    this.moving = false;
    this.lastX = null;
    this.lastY = null;
  }

  onMouseMove(e) {
    this.moving && this.onMove(e);
  }

  onMove(e) {
    if (this.lastX && this.lastY) {
      let dx = e.clientX - this.lastX;
      let dy = e.clientY - this.lastY;
      this.setState({
        X: this.state.X + dx * this.times,
        Y: this.state.Y + dy * this.times,
      });
    }
    this.lastX = e.clientX;
    this.lastY = e.clientY;
  }

  zoom = (e) => {
    if (!videoReg.test(this.props.ext)) {
      if (e.deltaY > 0) {
        if (this.scale >= 0.25) {
          this.scale -= 0.05;
        }
      } else {
        if (this.scale <= 4) {
          this.scale += 0.05;
        }
      }
      if (this.scale < 1) {
        this.times = 1 - this.scale + 1;
      } else {
        this.times = 1;
      }
    }
  };

  cptShow = () => {
    const { cpt } = this.props;
    this.setState({ cptModle: true });
    this.inputRef.current.value = cpt || "";
  };

  cptHide = () => {
    this.setState({ cptModle: false });
    this.inputRef.current.value = "";
  }

  addcpt = (val) => {
    this.cptHide();
    let cpt = this.props.cpt || '';
    if (cpt === val) {
      return;
    }
    this.props.blur(val);
  }

  render() {
    const { id, cpt, ext, closeDialog, onPiece, nextPage, blur } = this.props;
    let ViEl;
    if (videoReg.test(ext)) {
      ViEl = (
        <VideoPlayer
          poster={getFile.file_s(id)}
          src={getFile.file_b(id)}
        />
      );
    } else {
      ViEl = (
        <img
          src={this.state.load ? this.state.url : getFile.file_s(id)}
          alt={id}
        />
      );
    }
    return (
      <div className="dialog treeNodeUnselectable">
        <span className="close-dialog" onClick={closeDialog}>
          <Icon type="close" />
        </span>
        <div
          id="imgbody"
          className="dialog-boby"
          onWheel={this.zoom}
          onMouseUp={this.up}
        >
          <div
            id="imgbox"
            className="img-wrap-clear"
            ref={this.imgRef}
            onMouseDown={(e) => {
              this.onMouseDown(e);
            }}
            style={{
              transform: videoReg.test(ext)
                ? `scale(${this.scale}) translateX(-50%)translateY(-50%)`
                : `scale(${this.scale}) translateX(${this.state.X}px)translateY(${this.state.Y}px)`,
            }}
          >
            {!videoReg.test(ext) && !this.state.load && (
              <Icon
                className="icon-load"
                type="loading"
                style={{
                  fontSize: 70,
                  color: "#fff",
                }}
                spin
              />
            )}
            {ViEl}
          </div>
          <div
            className={`onPiece ${this.filg ? "hide" : ""}`}
            onClick={onPiece}
          >
            <span className="pre-arrow">
              <Icon type="left" />
            </span>
          </div>
          <div
            className={`nextPage ${this.filg ? "hide2" : ""}`}
            onClick={nextPage}
          >
            <span className="next-arrow">
              <Icon type="right" />
            </span>
          </div>

          {blur && (
            <div className={`operation ${this.filg ? "hide3" : ""}`}>
              <span
                ref={this.cptRef}
                className="ellipsis"
                style={{ color: "#FFF" }}
                onClick={this.cptShow}
              >
                {cpt
                  ? cpt
                  : `${i18next.t("adddescription")}...`}
              </span>
            </div>
          )}
        </div>

        <Modal
          title={i18next.t("AddCpt")}
          visible={this.state.cptModle}
          onOk={() => { this.addcpt(this.inputRef.current.value) }}
          onCancel={this.cptHide}
          getContainer={false}
          centered
        >
          <div className="cpt-inner-input">
            <input
              type="text"
              ref={this.inputRef}
              maxLength={100}
              autoFocus
              placeholder={i18next.t("enterdescription")}
            />
          </div>
        </Modal>
      </div>
    );
  }
}

export default ImageGallery;
