import React from 'react'
import Dropzone from 'react-dropzone';
import './index.scss';


export default class DropUp extends React.Component {
  state = {}

  render() {

    return (
      <Dropzone 
      onDrop={this.props.acceptedFiles} 
      noClick 
      preventDropOnDocument={false}
      accept="image/bmp,image/gif,image/jpeg,image/png,video/mp4,video/avi,video/m3u8,video/flv,video/rmvb">
        {({ getRootProps, getInputProps }) => (
          <div {...getRootProps()} className='DropUp_inner'>
            <input {...getInputProps()} />
            <p>{this.props.title}</p>
          </div>
        )}
      </Dropzone>
    );
  }
}