import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import { get } from 'mobx';
import { observer } from 'mobx-react';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
import FrameCell from '../../common/FrameCell';

@withTranslation() @observer
class Toframes extends Component {
  render() {
    const { framelist, id, framePlay, initload } = this.props;
    return (
      <>
        {initload ? framelist.length > 0 ? framelist.map(item => {
          return (
            <FrameCell
              key={item.node}
              id={id}
              style={{ width: item.w, height: item.h }}
              item={item}
              name={get(item, 'name')}
              framePlay={framePlay}
            />
          )
        }) : <Nodata /> : <Loading />}
      </>
    );
  }
}

export default Toframes;