import React, { PureComponent } from "react";
import { Tooltip } from "antd";
import { get } from "mobx";
import "./style.scss";
import { observer } from "mobx-react";


@observer
class ThumbnailCom extends PureComponent {
  state = {};
  render() {
    const {
      item,
      style,
      os,
      num,
      checkclick,
      b_img_show,
      index,
      imgBackground,
    } = this.props;
    const ps = os ? { visible: false } : {};

    return (
      <Tooltip title={item.cpt} overlayClassName="tooltip" {...ps}>
        <div
          className={`thumbnail ${get(item, "check") ? "selected" : ""}`}
          style={{ ...style, ...imgBackground  }}
          onClick={(e) => {
            if (num) {
              checkclick(e, item);
            } else {
              b_img_show(index, item.id);
            }
          }}
        >
          <span
            style={os ? { visibility: "inherit" } : {}}
            className="icon-check-s treeNodeUnselectable"
            onClick={(e) => {
              checkclick(e, item);
            }}
          >
            <i
              className="iconfont"
              style={get(item, "check") ? {} : { visibility: "hidden" }}
            >
              &#xe62d;
            </i>
          </span>
          <span className="sign">{index + 1}</span>
          {item.ext === "gif" && <i className="iconfont GIF-IMG">&#xe670;</i>}
          {item.cpt && <i className="iconfont icon">&#xe604;</i>}
          {/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(item.ext) && (
            <i className="iconfont v_icon">&#xe6ae;</i>
          )}
        </div>
      </Tooltip>
    );
  }
}

export default ThumbnailCom;
