import React, { Component } from "react";
import { Modal } from "antd";

export default class CustomModal extends Component {
  state = {
    inner: false,
  };

  componentDidMount() {
    this.resize();
    window.addEventListener("resize", this.resize);
  }

  resize = () => {
    if (window.innerWidth < 1100) {
      if (!this.state.inner) {
        this.setState({ inner: true });
      }
    } else {
      if (this.state.inner) {
        this.setState({ inner: false });
      }
    }
  };

  render() {
    const { visible, title, okBtnDis, handleOk, handleCancel } = this.props;
    return (
      <Modal
        title={title}
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
        width={this.state.inner ? "100%" : 1100}
        getContainer={false}
        okButtonProps={{ disabled: okBtnDis }}
        centered
        destroyOnClose
      >
        {this.props.children}
      </Modal>
    );
  }
}
